
// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import UiLoader from '@components/ui-loader'
import { useForm } from 'react-hook-form'
import classnames from 'classnames'
// ** Invoice List Sidebar
// import AddQuiz from '../add'

// ** Store & Actions
import { getfootballTotal, clearData, getFootballSquad, getFootballSquadadd, searchFootballSquad } from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
// import { selectThemeColors } from '@utils'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter } from 'react-feather'
import DataTable from 'react-data-table-component'
//import UiLoader from '@components/ui-loader'
import { Card, Row, Col, Label, Button, Spinner, Input, CardHeader, CustomInput } from 'reactstrap'
import { getAllSeries, getAllFixture, collectseriesid, collectfixtureid, getval } from '../../../Report/football/store/actions'
// ** Styles
import Select from 'react-select'
import Breadcrumbs from '@components/breadcrumbs'
import { selectThemeColors } from '@utils'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import AppCollapse from '@components/app-collapse'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"


// ** Table Header
const CustomHeader = () => {

  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">

    </div>
  )
}

const UsersList = () => {


  const typeOptions = [
    { value: '1', label: 'ST' },
    { value: '2', label: 'MID' },
    { value: '3', label: 'DEF' },
    { value: '4', label: 'GK' }
    
  ]

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.squad)
  const store1 = useSelector((state) => state.squad)
  const sto = useSelector((state) => state.reports)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [paginate, setPaginate] = useState(0)
  const [sid, setSid] = useState(0)
  const [fid, setFid] = useState(0)
  const [val, setVal] = useState(false)
  const [serilist, setSerilist] = useState("")
  const [fixlist, setFixlist] = useState("")
  const [refresh, setRefresh] = useState(false)
  const [click, setClick] = useState(false)
  const [error, setError] = useState(false)
  const [searchtype, setSearchType] = useState('')
  const [searchName, setSearchName] = useState('')
  const [page, setPage] = useState(1)
  // const [value] = useState(null)  
  const [dataPerPage, setDataPerPage] = useState('')
  const [series, setSeries] = useState('')

  // ** Get data on mount

  useEffect(() => {
    dispatch(getfootballTotal(page))
  }, [page])

  const idea1 = `{"series_id":"${sto.football_seriesid ? sto.football_seriesid : ''}", "fixture_id":"${sto.getval ? sto.football_fixtureid : ""}", "page":${currentPage}, "player_name":"${searchName ? searchName : ""}", "player_type":"${searchtype ? searchtype : ""}", "limit":"${dataPerPage ? dataPerPage : ''}" }`

  useEffect(() => {
    // const idea = `{"series_id":${sto.seriesid}, "fixture_id":"${sto.fixtureid}", "page":${currentPage}}`
    // const idea = `{"series_id":${sto.seriesid}, "fixture_id":"${sto.getval ? sto.fixtureid : ""}", "page":${currentPage}}`
    if (store.football_data_add.length !== 0) {
      dispatch(getFootballSquadadd(idea1))
      console.log(sto)
    } else {
      dispatch(getFootballSquad(currentPage))
    }
    dispatch(getAllSeries())
  }, [paginate])

  useEffect(() => {
    setCurrentPage(1)
    dispatch(getFootballSquadadd(`{"series_id":"${sto.football_seriesid ? sto.football_seriesid : ''}", "fixture_id":"${sto.getval ? sto.football_fixtureid : ""}", "page":${1}, "player_name":"${searchName ? searchName : ""}", "player_type":"${searchtype ? searchtype : ""}", "limit":"${dataPerPage ? dataPerPage : ''}" }`))
  }, [dataPerPage])

  useEffect(() => {
    if (store.isloading) {
      <Spinner />
      console.log("click")
      console.log(store.isloading)
    } else {
      setListData(store.football_data)
      console.log(store.isloading)
      console.log(store)
    }
  }, [store.isloading])

  //const columnsList = useCallback(columns(), [])
  // ** User filter options

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setPaginate(paginate + 1)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    setRowsPerPage(value)
    setCurrentPage(1)
  }

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.football_data.filter((userItem) => {

        if (userItem.player_type) {
          return userItem.player_type.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.football_data)
    }
  }, [searchTerm])

  useEffect(() => {
    console.log('Mounted')
    return () => {
      console.log('Unmounted')
      dispatch(clearData())
    }
  }, [])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = store.football_data_add?.data?.length > 0 ? store.football_data_add?.last_page : store.searchFootballSquad?.data?.length > 0 ? store.searchFootballSquad?.last_page : store.football_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (store.football_data_add?.data?.length >= 0) {
      return store.football_data_add?.data
    } else if (store.searchFootballSquad?.data?.length) {
      return store.searchFootballSquad?.data
    } else if (listData.length === 0 && isFiltered) {
      return []
    }
  }

  const columns = [
    {
      name: '#',
      minWidth: '10px',
      cell: (row, pos) => `#${store.football_page?.data?.length ? store.football_page?.from + pos : store.football_page?.data?.from + pos}`
    },
    {
      name: 'Name',
      minWidth: '200px',
      selector: 'player_name',
      sortable: true,
      cell: (row) => row.player_name
    },
    {
      name: <span>Name</span>,
      minWidth: '160px',
      selector: 'team_name',
      sortable: true,
      cell: (row) => (
        <Row>
          <img src={row.path} style={{ height: '40px', width: '40px' }} />
          <Col style={{ width: '200px', overflow: 'hidden', textOverflow: 'ellipsis' }}>{row.team_name}</Col>
        </Row>
      )

    },

    {
      name: 'Type',
      minWidth: '200px',
      selector: 'player_type',
      sortable: true,
      cell: (row) => row.player_type
    },
    {
      name: 'Credits',
      minWidth: '200px',
      selector: 'player_value',
      sortable: true,
      cell: (row) => row.player_value
    }
  ]

  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit } = useForm({ defaultValues })

  const fixture = sto.football_fixture_name?.map(function (fix) {
    return { value: fix.id, label: fix.fixture_name }
  })

  const filterid = sto.football_series_name?.filter(s => s.series_name === serilist)
  const filterid2 = sto.football_fixture_name?.filter(f => f.fixture_name === fixlist)
  const filterall = sto.football_fixture_name?.map(f => f.fixture_name)

  useEffect(() => {
    dispatch(getAllFixture(serilist))
    console.log(serilist)
    console.log(store.football_fixture_name)
    console.log(store)
    console.log(sid)
  }, [serilist])

  const idea = `{"series_id":"${serilist ? serilist : ''}", "fixture_id":"${click ? fixlist : ""}", "page":${1}, "player_name":"${searchName ? searchName : ""}", "player_type":"${searchtype ? searchtype : ""}", "limit":"${dataPerPage ? dataPerPage : ''}" }`
  const submit = (e) => {
    e.preventDefault()
    setVal(true)
    console.log(filterall.map(f => f === fixlist))
    setCurrentPage(1)
    //  const idea = `{"series_id":${serilist}, "fixture_id":"${fixlist}", "page":${currentPage}}`
    console.log(fixlist)
    if (serilist === "") {
      setError(true)
      dispatch(getFootballSquadadd(idea))
    } else {
      dispatch(getFootballSquadadd(idea))
    }
    dispatch(collectseriesid(serilist))
    dispatch(collectfixtureid(fixlist))
  }

  useEffect(() => {
    if (store?.football_data_add.length !== 0) {
      setListData(store?.football_data_add)
    } else {
      if (store?.football_data_add.length === 0 && val) {
        setListData(store?.football_data_add)
      } else {
        setListData(store?.football_data)
      }
    }
  }, [submit])

  const serieslist = (e) => {
    setSeries(e)
    setVal(false)
    console.log(error)
    setError(false)
    setSerilist(e?.value)
    setRefresh(true)
    dispatch(getval(false))
    // dispatch(getAllFixture(filterid[0]?.id))
    setSid(filterid[0]?.id)
    console.log(filterid[0]?.id)
    setClick(false)
    // return store.football_series_name.filter(s => s.series_name === list)
  }

  const fixturelist = (e) => {
    const datas = e.map(e => e.value)
    setFixlist(datas)
    setClick(true)
    dispatch(getval(true))
    setRefresh(false)
    console.log(e.map(e => e.value))
    setFid(filterid2[0]?.id)
    console.log(filterid2[0]?.id)
    console.log(fid)
    // return store.football_series_name.filter(s => s.series_name === list)
  }


  const handleKeyPress = (e) => {
    if (e.keyCode === 13) {
      setCurrentPage(1)
      dispatch(getFootballSquadadd(idea))
      dispatch(collectseriesid(serilist))
      dispatch(collectfixtureid(fixlist))
    }
  }

  useEffect(() => {
    dispatch(searchFootballSquad(currentPage, searchName, searchtype))
  }, [currentPage])

  console.log(searchtype)
  console.log(searchName)

  const typeHandler = (value) => {
    setSearchType(value)
  }

  // ************************************************************************************* //

  const options = []
  for (let i = 0; i < store1.football_total?.data?.total; i++) {
    if (page === store1.football_total?.data?.current_page) {
      options.push({
        value: `${store1.football_total?.data?.data[i]?.id}`,
        label: `${store1.football_total?.data?.data[i]?.series_name}`
      })
    }

  }
  const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
  const optionsPerPage = 10

  const loadOptions = async (search, prevOptions, page) => {
    await sleep(1000)
    if (page === store1.football_total?.data?.current_page) {
      const nextData = store1.football_total?.data?.data?.map((nextData) => { return { value: nextData.id, label: nextData.series_name } })
      const scrollLength = [...prevOptions, ...nextData]
      let filteredOptions
      if (!search) {
        filteredOptions = scrollLength.length > 10 ? scrollLength : options
      } else {
        const searchLower = search.toLowerCase()
        filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
        )
      }

      const hasMore = Math.ceil(store1.football_total?.data?.total / optionsPerPage) > page
      const slicedOptions = filteredOptions.slice(
        (page - 1) * optionsPerPage,
        page * optionsPerPage
      )

      return {
        options: slicedOptions,
        hasMore
      }
    }
  }
  const defaultAdditional = {
    page: 1
  }

  const loadPageOptions = async (q, prevOptions, { page }) => {
    setPage(page)
    const { options, hasMore } = await loadOptions(q, prevOptions, page)
    return {
      options: [...options],
      hasMore,

      additional: {
        page: page + 1
      }
    }
  }

  // ************************************************************************************* //

  const data = [
    {
      title: <div><Filter size={18} /> Filter</div>,
      content: (
        <>
          <Row>
            <Col className='mb-1' md='6' sm='12'>
              <Label>Series</Label>
              <AsyncPaginate
                menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                isClearable={true}
                className={classnames('react-select')}
                classNamePrefix='select'
                additional={defaultAdditional}
                value={series}
                loadOptions={loadPageOptions}
                onChange={(e) => serieslist(e)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
            <Col className='mb-1' md='6' sm='12'>
              <Label>Fixture</Label>
              <Select
                isDisabled={sto.fixloader || !serilist}
                onChange={(e) => fixturelist(e)}
                theme={selectThemeColors}
                value={refresh ? "" : fid}
                className='react-select'
                classNamePrefix='select'
                isMulti
                // defaultValue={fixture[0]}
                options={fixture ? fixture : ""}
                placeholder='Select football fixture'
                isClearable={true}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
          </Row>
          <Row>
            <Col className='mb-1 mt-1' xl={6}>
              <Label>Player Name:</Label>
              <Input
                id="search-invoice"
                className="w-100"
                type="text"
                value={searchName}
                placeholder='Enter player name'
                onChange={(e) => setSearchName(e.target.value)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
            <Col className='mb-1 mt-1' xl={6}>
              <Label>Player Type:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => typeHandler(e?.label)}
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={typeOptions}
                placeholder='Search player type'
                isClearable={true}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
          </Row>
          <Button.Ripple
            color='primary'
            onClick={(e) => handleSubmit(submit(e))}
          >Submit</Button.Ripple>
        </>
      )
    }
  ]

  return (
    <Fragment>
      <Breadcrumbs breadCrumbTitle='Football Squad' breadCrumbActive='List' />
      <AppCollapse data={data} type='border' accordion className='mb-1' />
      <Card>
        <CardHeader>
          <Col xl="12" className="d-flex align-items-center p-0">
            <div className="d-flex align-items-center w-100">
              <Label for="rows-per-page">Show</Label>
              <CustomInput
                className="form-control mx-50"
                type="select"
                id="rows-per-page"
                value={dataPerPage}
                onChange={(e) => setDataPerPage(e.target.value)}
                style={{
                  width: '5rem',
                  padding: '0 0.8rem',
                  backgroundPosition:
                    'calc(100% - 3px) 11px, calc(100% - 20px) 13px, 100% 0'
                }}
              >
                <option value="10">10</option>
                <option value="50">50</option>
                <option value="100">100</option>
              </CustomInput>
              <Label for="rows-per-page">Entries</Label>
            </div>
          </Col>
        </CardHeader>
        <DataTable
          noHeader
          // pagination
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              currentPage={currentPage}
              handleFilter={handleFilter}
              setListData={setListData}
              football_data={store.football_data}
              football_data_add={store.football_data_add}
              setCurrentPage={setCurrentPage}
            />
          }
        />
      </Card>
      {(store.football_data_add?.data?.length === 0 || store1.football_total?.data?.data?.length === undefined  || store1.football_total?.data?.data?.length === 0) ? "" :   <CustomPagination />}         
    </Fragment>
  )
}

export default UsersList